<template>
    <div>
        <el-row class="top-bar" type="flex" justify="space-between">
            <el-col :span="4" :lg="3" :xs="5">
                <div class="logo grid-content">至尊宝</div>
            </el-col>
            <el-col class="user-info" :span="8">
                <el-button type="text" size="mini" @click="toNews()">
                    <el-badge is-dot class="news-btn icon icon-remind"></el-badge>
                </el-button>
                <!-- <span class="icon icon-remind"></span> -->
                <span class="user-name grid-content">{{userName}}</span>
                <el-button type="text" @click="quit()">退出</el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col tag="aside" :span="4" :lg="4" :xs="5" class="side-bar">
                <el-menu router unique-opened :default-active="activeIndex" class="el-menu-vertical-demo">
                    <el-submenu index="index">
                        <template slot="title">运营信息</template>
                        <el-menu-item index="index" :active="true">首页轮播</el-menu-item>
                        <el-menu-item index="system-push">系统推送信息</el-menu-item>
                        <el-menu-item index="operating-articles">运营文章</el-menu-item>
                        <el-menu-item index="vehicle-manage">车型库管理</el-menu-item>
                    </el-submenu>
                    <el-submenu index="2">
                        <template slot="title">用户</template>
                        <el-submenu index="user-info">
                          <template slot="title">用户信息</template>
                          <el-menu-item index="user-info">用户信息</el-menu-item>
                          <el-menu-item index="car-owner-info">车东信息</el-menu-item>
                        </el-submenu>
                        <el-menu-item index="vehicle-info">车辆信息</el-menu-item>
                        <el-menu-item index="ticket-info">罚单信息</el-menu-item>
                        <el-menu-item index="user-credit">用户信用</el-menu-item>
                    </el-submenu>
                    <el-menu-item index="order">订单</el-menu-item>
                    <el-submenu index="4">
                        <template slot="title">财务</template>
                        <!-- <el-menu-item index="user-account">用户账户</el-menu-item> -->
                        <!-- <el-menu-item index="user-account-income-expense">用户账户收支明细</el-menu-item> -->
                        <el-menu-item index="platform-account">平台账户</el-menu-item>
                        <el-menu-item index="withdraw">提现管理</el-menu-item>
                    </el-submenu>
                    <el-submenu index="5">
                        <template slot="title">系统设置</template>
                        <el-menu-item index="account-manage">账户管理</el-menu-item>
                        <el-menu-item index="my-account">我的账户</el-menu-item>
                    </el-submenu>
                    <el-menu-item index="news">消息</el-menu-item>
                </el-menu>
            </el-col>
            <el-col :span="19" :lg="20" :xs="18" id="main">
                <router-view :API="API"></router-view>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
            userName: JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
        }
    },
    computed: {
        // 导航高亮
        activeIndex() {
            return this.$store.state.activeIndex
        }
    },
    props: ['API'],
    methods: {
        quit() {
            this.$confirm('是否退出?', '提示', {type: 'warning'})
                .then(() => {
                    sessionStorage.removeItem('zhizunbao_login')
                    this.userName = ''
                    this.$message({
                        message: "退出成功",
                        type: 'success'
                    })
                    this.$router.push('/')
                })
                .catch(() => {})
        },
        toNews() {
            this.$router.push('/news')
        }
    },
    mounted(){

    }
}
</script>

<style lang="scss">
.news-btn {
    font-size: 18px;
}
.el-badge__content.is-dot {
    position: absolute;
    top: -3px;
    top: -99999px;
}
</style>



